.border_btn02 {
  display: block;
  margin: auto;
  width: 100%;
  max-width: 350px; /* ボタン幅 */
  position: relative;
  color: #b7262e; /* 文字色 */
  padding: 1em 2em;
  font-weight: 400;
  text-decoration: none;
  text-align: center;
  transition: 0.3s;
}

.border_btn02::before,
.border_btn02::after {
  position: absolute;
  width: 100%;
  height: 2px; /* 線幅 */
  content: '';
  -webkit-transition: all .3s;
  transition: all .3s;
  background: #b7282e; 
}

.border_btn02::before {
  top: 0;
  left: 0;
}

.border_btn02::after {
  right: 0;
  bottom: 0;
}

/* マウスオーバーした際のデザイン */
.border_btn02:hover:before,
.border_btn02:hover:after {
  width: 0;
}

